<template>
  <canvas ref="container" style="width: 100%;height: 100%" :style="{}"></canvas>
</template>

<script>
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
export default {
  name: "threeFlyLine",
  props: {
    h: Number
  },
  data() {
    return {
      controll: null,
      render: null,
      camera: null,
      scene: null,
      player: true,
      cube: null,
      startP: null,
      endP: null,
      system: null,
      cameraOpt: {
        angle: 70,
        min: 0.1,
        max: 1000
      },
      path: null,
      deltime: 0,
      speed: 0.01,
      catchObj: null
    };
  },
  mounted() {
    console.log(1);
    this.renderAll();
  },
  methods: {
    renderLine(curve, lens) {
      const p = [];
      for (let i = 0; i < lens; i++) {
        if (this.deltime + this.speed * i <= 1) {
          p.push(curve.getPointAt(this.deltime + this.speed * i));
        }
      }
      if (p.length <= 1) {
        this.deltime = 0;
        return;
      }
      // const size = 10;
      const cacurve = new THREE.CatmullRomCurve3([...p]);
      const points = cacurve.getPoints(50);
      const shaderMaterial = new THREE.LineBasicMaterial({ color: 0xff0000 });
      const curveObject = new THREE.Line(
        new THREE.BufferGeometry().setFromPoints(points),
        shaderMaterial
      );
      if (this.catchObj) {
        this.scene.remove(this.catchObj);
        this.catchObj.geometry.dispose();
        this.catchObj.material.dispose();
      }
      this.catchObj = curveObject;
      this.scene.add(curveObject);
      this.deltime =
        this.deltime + this.speed > 1 ? 0 : this.deltime + this.speed;
    },
    initControll() {
      this.controll = new OrbitControls(this.camera, this.render.domElement);
    },
    renderAll() {
      this.renderInit();
      this.renderPoints();
      this.path = this.renderSystem(this.startP, this.endP, 3);
      this.initControll();
      this.animation();
    },
    renderInit() {
      const w = this.$refs.container.clientWidth;
      const h = this.$refs.container.clientHeight;
      this.render = new THREE.WebGLRenderer({
        canvas: this.$refs.container
      });
      this.render.setSize(w, h);
      // this.$refs.container.appendChild(this.render.domElement);
      this.camera = new THREE.PerspectiveCamera(
        this.cameraOpt.angle,
        w / h,
        this.cameraOpt.min,
        this.cameraOpt.max
      );
      this.camera.position.set(0, -80, 80);
      this.camera.lookAt(0, 0, 0);
      this.scene = new THREE.Scene();
    },
    renderSystem(s, e, n) {
      console.log(s, e, n);
      const lens = [s];
      const x = e.x - s.x;
      const y = e.y - s.y;
      const z = e.z - s.z;
      for (let k = 0; k < n; k++) {
        lens.push(
          new THREE.Vector3(
            x * Math.random() + s.x,
            y * Math.random() + s.y,
            z * Math.random() + s.z
          )
        );
      }
      lens.push(e);
      const curve = new THREE.CatmullRomCurve3([s, ...lens, e]);
      // const points = curve.getPoints(50);
      // const geometry = new THREE.BufferGeometry().setFromPoints(points);
      //
      // const material = new THREE.LineBasicMaterial({ color: 0xff0000 });
      //
      // // Create the final object to add to the scene
      // const curveObject = new THREE.Line(geometry, material);

      // this.scene.add(curveObject);
      return curve;
    },
    renderPoints() {
      this.startP = new THREE.Vector3(100, 100, 0);
      this.endP = new THREE.Vector3(-100, -100, 0);
      let geometry = new THREE.Geometry();
      geometry.vertices.push(this.startP);
      geometry.vertices.push(this.endP);
      geometry.colors.push(new THREE.Color(0xff0000));
      geometry.colors.push(new THREE.Color(0xffff00));
      const material = new THREE.PointsMaterial({
        size: 20,
        sizeAttenuation: true,
        vertexColors: THREE.VertexColors,
        transparent: true,
        opacity: 0.7
      });
      /* 批量管理点 */
      const points = new THREE.Points(geometry, material);
      this.scene.add(points);
    },
    animation() {
      this.renderLine(this.path, 10);
      this.controll.update();
      this.render.render(this.scene, this.camera);
      requestAnimationFrame(() => {
        if (this.player) {
          this.animation();
        }
      });
    }
  }
};
</script>

